export default "<template>\n  <div class=\"MonHocToan\">\n    <raw-loader :mytxt=\"require('!raw-loader!./UploadIformModel.vue').default\"></raw-loader>\n    <h1>UploadIformModel page</h1>\n    <com-view-imag ref=\"listImg\"></com-view-imag>\n    <div>\n      <form enctype=\"multipart/form-data\" @submit.stop.prevent=\"onUpload\">\n        <img ref=\"outImage\" class=\"mb-1\" width=\"50\" height=\"70\" /><br>\n        <input type=\"text\" ref=\"Ten\" ><br>\n        <input type=\"file\" id=\"file\" ref=\"file\" class=\"m-1\" @change=\"onFileChange\" /><br>\n        <button type=\"submit\" class=\"mr-1\">Upload</button>\n      </form>\n    </div>\n  </div>\n</template>\n<script>\nimport $ from \"jquery\";\nimport ComViewImag from \"./ComViewImage.vue\"\nexport default {\n  name: 'Tpl',\n  components: {\n    ComViewImag\n  },\n  methods: {\n    onFileChange() {\n      let files=$(this.$refs.file).get(0).files;\n      let file=files[0];\n      this.getReadAsDataURL(file).then(res=>{\n        $(this.$refs.outImage).attr(\"src\",res);\n        // alert(res);\n        // data:image/jpeg;base64,/9j/4AAQSkZJRgABAQEAYABgAAD/2wBDAAUDBAQEAwUEBAQFBQUGBwwIBwcHBw8LCwkMEQ8SEhEPERETFhwX  \n      })\n    },\n    getReadAsDataURL(file) {\n      return new Promise((resolve, reject) => {\n        let reader = new FileReader();\n        reader.onload = () => {\n          resolve(reader.result);\n        };\n        reader.onerror = reject;\n        reader.readAsDataURL(file);\n      });\n    },\n    onUpload() {\n      let len = $(this.$refs.file).get(0).files.length;\n      if ( len <= 0 || $(this.$refs.Ten).val() == ''  ) {\n        alert('ban chua chon file de upload or Ten is blank');\n        return;\n      }\n      let files=$(this.$refs.file).get(0).files;\n      var fileData = new FormData();\n      for (var i = 0; i < files.length; i++) {\n        fileData.append(\"File\", files[i]);\n      }\n      fileData.append(\"Ten\", $(this.$refs.Ten).val());\n      let that = this;\n      $.ajax({\n        url: \"/api/FileUpload/Upload3\", ///Home/UploadFiles\n        type: \"POST\",\n        contentType: false, // Not to set any content header\n        processData: false, // Not to process data\n        data: fileData,\n        success: function (result) {\n          alert(JSON.stringify(result));\n          that.$refs.listImg.loadListImg();\n          $(that.$refs.file).val(\"\");\n          $(that.$refs.outImage).attr(\"src\",'');\n          $(that.$refs.Ten).val(\"\");\n        },\n        error: function (err) {\n          alert(err.statusText);\n        },\n      });\n  }\n}\n}\n</script>\n";